.card1,
.card2,
.card3 {
    width: 350px;
    height: 512px;
    background-color: #FFFFFF;
    border-radius: 20px;
    margin-right: 34px;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    position: relative;
}

.picture img {
    width: 350px;
    height: 245px;
    margin-bottom: 24px;
    border-radius: 20px 20px 0px 0px; 
}

.cards {
    display: flex;
}

.check {
    margin-top: 222px;
}

.name {
    color: #000000;
    font-weight: bold;
    font-size: 32px;
    line-height: 56px;
    margin-bottom: 26px;
    margin-left: 30%;
}

.line {
    width: 56px;
    border: 2px solid black;
    border-radius: 5px;
    margin-bottom: 80px;
    margin-left: 45%;
}

.title {
    font-family: Mulish;
    width: 265px;
    color: #000000;
    font-weight: bold;
    font-size: 21px;
    line-height: 32px;
    margin-bottom: 12px;
}

.description {
    width: 266px;
    color: #7D7987;
    font-size: 16px;
    line-height: 28px;
    font-weight: lighter;
    margin-bottom: 25px;
}

.arrow {
    color: #4089ED;
}

.arrow img {
    width: 14px;
    margin-left: 15px;
    color:  #4089ED;
}

.text {
    margin-left: 33px;
}

.button {
    font-size: 18px;
    width: 200px;
    height: 56px;
    border: 1px solid #4089ED;
    border-radius: 55px;
    background: white;
    color: #4089ED;
    margin-top: 70px;
    margin-left: 38%;
    margin-bottom: 200px;
}

.button:hover {
    background: #4089ED;
    color: white;
}

.pic {
    position: absolute;
    z-index:0;
    margin-left: 40%;
}

.element1 {
    position: absolute;
    margin-top: 120px;
    margin-left: -50px;
}

.element2 {
    position: absolute;
    margin-top: 250px;
    margin-left: 480px;
}

@media (max-width: 1200px) {
    .check {
        max-width: 960px;
        margin-top: 150px;
        
    }
    .name {
        margin-left: 26%;
    }
    .cards {
        margin-left: 85px;
    }
    .card1,
    .card2 {
        width: 300px;
        height: 450px;
        background: white;
        margin-left: -10px;
    }
    .picture img {
        width: 300px;
        height: 195px;
    }
    .text {
        margin-left: 20px;
    }
    .description {
        margin-bottom: 25px;
    }
    .card3 {
        display: none;
    }
    .button {
        margin-bottom: 80px;
    }
}

@media (max-width: 576px) {
    .check {
        max-width: 350px;
    }
    .card2 {
        display: none;
    }
    .name {
        width: 200px;
        line-height: normal;
        text-align: center;
        font-size: 26px;
        margin-left: 80px;
    }
    .line {
        border: 1px solid black;
        margin-left: 155px;
    }
    .card1 {
        margin-left: -48px;
        margin-top: -30px;
    }
    .button {
        margin-left: 85px;
        margin-top: 20px;
    }
}