.background {
    max-width: 1120px;
    height: 425px;
    background: linear-gradient(191.49deg, #67C3F3 9.05%, #5A98F2 76.74%);
    border-radius: 24px;
    margin-top: 266px;
}

.next-slick-arrow,
.prev-slick-arrow {
    color: #458FF6;
    font-size: 48px;
    margin-top: 253px;
    position: absolute;
}

.next-slick-arrow {
    right: 510px;
    top: 152px;
    
}

.prev-slick-arrow {
    left: 430px;
    top: 152px;
}

.slick-dots {
    right: 40px;
}

.slick-dots li {
	display: inline-block;
    width: 10px;
    height: 10px;
    margin: 10px;
    cursor: pointer;
    border: none;
    border-radius: 50%;
    background-color: #5B9BF3;
    box-shadow: inset 1px 1px 1px 1px rgba(0,0,0,0.5); 
    top: 47px;
    
}

.slick-dots .slick-active {
    width: 12px;
    height: 12px;
    margin: 10px;
    background-color: #5B9BF3; 
    opacity: 0.3;
}

.element {
    position: absolute;
    margin-left: 1035px;
    bottom: 322px;
}

.title {
    color: #FFFFFF;
    font-weight: bold;
    font-size: 36px;
    line-height: 48px;
    margin-top: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px;
}

.line {
    width: 56px;
    border: 2px solid white;
    border-radius: 6px;
    position: relative;
    left: 47%;
    margin-bottom: 77px;
}

.avatar {
    width: 138px;
    height: 138px;
    object-fit: cover;
    border-radius: 50%;
    border: 3px solid white;
    
}

.items {
    visibility: visible;
    margin-top: 25px;
    margin-left: 30px;
}

.name {
    color: #FFFFFF;
    font-family: 'Mulish';
    font-weight: bold;
    font-size: 22px;
    line-height: 48px;
    margin-bottom: -15px;
}

.post {
    color: rgba(255, 255, 255, 0.85);
    font-weight: normal;
    font-size: 18px;
    line-height: 48px;
}

.text {
    width: 415px;
    height: 120px;
    color: rgba(255, 255, 255, 0.9);
    font-weight: normal;
    font-size: 19px;
    line-height: 30px;
    white-space: pre-line;
}
.feedback {
    display: flex;
    justify-content: space-between;
}

.left_side {
    display: flex;
    margin-left: 118px;
}

.right_side {
    margin-right: 118px;
}

.slick-dots li button:before {
    display: none;
  }

  @media (max-width: 1200px) {
    .background {
        max-width: 950px;
        margin-left: 20px;
        margin-top: 80px;
    }
    .next-slick-arrow {
        right: 450px;
        top: 58px;
        
    }
    .prev-slick-arrow {
        left: 365px;
        top: 58px;
        
    }
}

@media (max-width: 991px) {
    

}

@media (max-width: 768px) {
    .background {
        max-width: 650px;
        height: 370px;
        margin-top: 10px;
        margin-left: 60px;
    }
    .feedback {
        margin-left: 100px;
        display: block;
        margin-top: -40px;
    }
    .element {
        display: none;
    }
    .feedback img {
        display: none;
    }
    .next-slick-arrow {
        top: -3px;
        right: 300px;        
    }
    .prev-slick-arrow {
        top: -3px;
        left: 282px;
    }
    .title {
        font-size: 30px;
        margin-top: 20px;
    }
    .text {
        margin-left: 18px ;
        margin-top: 20px;
        font-size: 18px;
        text-align: center;
    }
    .items {
        width: 185px;
        margin-left: 20px;
        margin-top: -20px;
        text-align: center;
    }
    .name {
        font-size: 20px;
        margin-bottom: -25px;
    }
    .post {
        font-size: 16px;
    }
    .line {
        border: 1px solid white;

    }
    .slick-dots {
        right: 5px;
    }

}

@media (max-width: 576px) {
    .background {
        max-width: 300px;
        margin-left: 38px;
        margin-top: 20px;
        height: 420px;
    }
    .feedback {
        margin-left: 10px;
    }
    .title {
        width: 200px;
        line-height: normal;
        font-size: 22px;
        text-align: center;
        margin-left: 50px;
    }
    .line {
        margin-left: -16px;
    }
    .name {
        line-height: normal;
        font-size: 15px;
        margin-bottom: -10px;
    }
    .post {
        font-size: 15px;
    }
    .left_side {
        margin-left: 30px;
    }
    .items {
        margin-top: -10px;
    }
    .text {
        width: 230px;
        text-align: center;
        font-size: 16px;
        margin-left: 25px;
        margin-top: 5px;
    }
    .next-slick-arrow {
        top: -3px;
        right: 100px;        
    }
    .prev-slick-arrow {
        top: -3px;
        left: 105px;
    }
    .slick-dots {
        left: 3px;
        top: 92%;
    }
}